import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LinkedinService } from '../linkedin.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-linkedin',
  templateUrl: './linkedin.component.html',
  styleUrls: ['./linkedin.component.css']
})
export class LinkedinComponent implements OnInit {

  constructor(private router: Router, private activatedRoute: ActivatedRoute, private link: LinkedinService, private http: HttpClient) { }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      const code = params['code'];
      const state = params['state'];
      const error = params['error'];
      const errorDescription = params['error_description'];

      if (code) {
        console.log('paejo');

        this.link.postData(code);
      }

      if (error) {
        console.log('nepaejo');
      }

    });
  }

  test() {
    this.link.getData();
    this.router.navigateByUrl('linkedin');
  }



}







