import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

interface UserResponse {
  access_token: String;
  expires_in: String;
}

@Injectable()
export class LinkedinService {

  authToken: String = '';
  baseUrl: String = 'https://www.linkedin.com/oauth/v2/accessToken?';
  grantType: String = 'authorization_code';
  redirectUri: String = 'https://edvinasgestautas.github.io/linkedin';
  clientId: String = '86wm601visw1wd';
  clientSecret: String = 'XQDbxw1vfFn0c0Xj';
  headers = {
    headers: new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded',
    })
  };

  constructor(private http: HttpClient) { }

  postData(code: String) {
    this.http.post<UserResponse>(
      this.baseUrl +
      'grant_type=' + this.grantType +
      '&code=' + code +
      '&redirect_uri=' + this.redirectUri +
      '&client_id=' + this.clientId +
      '&client_secret=' + this.clientSecret, {
      }, this.headers)
      .subscribe(
        res => {
          console.log(res.access_token);
          this.authToken = res.access_token;
        },
        err => {
          console.log('Error occured' + err);
        }
      );
  }

  getData() {
    // tslint:disable-next-line:max-line-length
    this.http.get<JSON>('https://api.linkedin.com/v1/people/~:(id,num-connections,picture-url,location,positions,recommendations-received,email-address)?format=json&oauth2_access_token=' + this.authToken).
      subscribe(data => {
        console.log(data);
      });

  }

}



